
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data() {
return {
code: '',
password: '',
showGet: true,
isGet: false,
timer: null,
count: '',
isOk: true,
isPwd: 'password',
isShow: true
}
},
created() {
this.clickGetCode()
this.phoneNum = this.$route.query.phone
},
methods: {
clickGetCode() {
const TIME_COUNT = 60
if (!this.timer) {
this.count = TIME_COUNT
this.showGet = false
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--
} else {
this.showGet = true
clearInterval(this.timer)
this.timer = null
}
}, 1000)
}
},
clickOk() {
this.$vux.loading.show()
setTimeout(() => {
this.$axios.post('/SmartHome/updatePwd', {
phone: this.phoneNum,
code: this.code,
password: this.password
})
.then((res) => {
console.log(res)
if(res.data == 'updatePwdFailure'){
this.$vux.alert.show({
content: '验证码错误！'
})
}else{
this.$router.push('/login')
}
})
this.$vux.loading.hide()
}, 500)
},
clickShow() {
this.isShow = !this.isShow
if (this.isShow == false) {
this.isPwd = 'text'
} else {
this.isPwd = 'password'
}
}
},
watch: {
code: function () {
if (this.code !== '' && this.password !== '') {
this.isOk = false
} else {
this.isOk = true
}
},
password: function () {
if (this.code !== '' && this.password !== '') {
this.isOk = false
} else {
this.isOk = true
}
}
},
components: {
XHeader,
XButton,
XInput,
Group
},
}



