
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data() {
return {
joinCode: '',
familyId: '',
familyName: '',
familyAddress: '',
data: []
}
},
created(){
this.isManager()
},
methods: {
isManager(){
var that = this
this.$axios.post(`/SmartHome/is_manager`,{
familyId: this.$route.query.familyId,
userId: JSON.parse(localStorage.mydata).user.id
})
.then((res) => {
console.log(res)
if(res.data == false){
this.$vux.alert.show({
content: '你不是管理员,不能操作！',
onShow(){
that.$router.go(-1)
}
})
}else{
this.clickInit()
this.clickGetCode()
}
})
},
clickInit(){
var familyId = this.$route.query.familyId
this.$axios.get(`/SmartHome/get_family/${familyId}`)
.then((res) => {
this.data = res.data[0]
this.familyName = res.data[0].familyName
this.familyAddress = res.data[0].familyAddress
})
},
clickGetCode(){
this.joinCode = Number(Math.random().toString().substr(3, 6) + Date.now()).toString(36)
console.log(this.joinCode);
setTimeout(() => {
var id = JSON.parse(localStorage.mydata).user.id
var familyId = this.$route.query.familyId
this.$axios.post('/SmartHome/add_family_uniquecode', {
userId: id,
familyId: familyId,
uniqueCode: this.joinCode
})
.then((res) => {
console.log(res)
})
}, 1000)
},
clickSave() {
var that = this
var familyId = this.$route.query.familyId;
var userId = JSON.parse(localStorage.mydata).user.id
this.$axios.post('/SmartHome/ud_family', {
userId: userId,
familyId: familyId,
familyAddress: that.familyAddress,
familyName: that.familyName
})
.then((res) => {
this.clickInit()
})
this.$vux.loading.show({
text: '加载中'
})
setTimeout(() => {
var that = this
this.$vux.loading.hide()
that.$router.push('/my_family')
}, 1000)
},
},
components: {
Actionsheet,
XHeader,
Qrcode,
Group,
Cell,
XButton,
XInput,
Flexbox,
FlexboxItem
}
}



