
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data() {
return {
showLogin: false
}
},
created() {
setTimeout(() => {
this.showLogin = true
}, 2000)
},
methods: {
clickLogin() {
this.$router.push('/userLogin')
},
clickRegister() {
this.$router.push('/register')
}
},
components: {
XButton
}
}



