
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data() {
return {
showMore: false,
data: [],
familyImg: 'http://www.xiaoxiangba.com/assets/img/bg01.jpg'
}
},
created() {
this.clickInit()
},
methods: {
clickInit(){
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.get(`/SmartHome/get_family?id=${id}`)
.then((res) => {
this.data = res.data
})
},
clickDetele(familyId){
var userId = JSON.parse(localStorage.mydata).user.id
this.$axios.post('/SmartHome/del_family', {
userId: userId,
familyId: familyId
})
.then((res) => {
console.log(res)
if(res.data == 'deleteFamilyFailure'){
this.$vux.alert.show({
content: '你不是管理员！不能删除'
})
}
this.clickInit()
})
},
clickMore() {
this.showMore = !this.showMore
},
createFamily() {
this.$router.push('/my_family/create_family')
},
joinFamily() {
this.$router.push('/my_family/join_family')
}
},
components: {
XHeader,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
}
}



