
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Range from 'vux/src/components/range/index.vue'


export default {
data() {
return {
percent1: 0,
data: [],
deviceName: ''
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var deviceId = this.$route.query.deviceId
this.$axios.get(`/SmartHome/get_device/${deviceId}`)
.then((res) => {
console.log(res)
this.data = res.data[0]
this.deviceName = res.data[0].name
})
},
clickUpdata() {
var that = this
this.$vux.confirm.prompt('请输入设备名', {
onConfirm (value) {
console.log(value);
that.$axios.post(`/SmartHome/up_device`,{
id: that.$route.query.deviceId,
name: value
})
.then((res) => {
if(res.data == 'update success'){
that.deviceName = value
}
this.data = res.data[0]
})
}
})
},
clickDelDevice(){
var deviceId = this.$route.query.deviceId
var userId = JSON.parse(localStorage.mydata).user.id
console.log(this.data.familyId);
this.$axios.post('/SmartHome/del_device', {
familyId: this.data.familyId,
userId: userId,
deviceId: deviceId
})
.then(res => {
if(res.data == 'delete Success'){
this.$router.push({path: '/user_device',query: {familyId: 9}})
}
})
}
},
components: {
XCircle,
XButton,
Range,
XHeader,
Actionsheet,
Group,
Cell,
XSwitch,
Toast
}
}



