
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data() {
return {
joinCode: '',
familyId: ''
}
},
methods: {
clickInit(){
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.get(`/SmartHome/get_family?id=${id}`)
.then((res) => {
this.familyId = res.data[0].id
})
},
joinList() {
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.post('/SmartHome/add_member', {
userId: id,
uniqueCode: this.joinCode
})
.then((res)=>{
console.log(res.data)
if(res.data == '已加入家庭，不能重复加入'){
this.$vux.alert.show({content:'已加入家庭，不能重复加入！'})
}else if(res.data == 'joinFamilySuccess') {
this.$router.push('/my_family')
}else if(res.data == 'can not find family by uniquecode'){
this.$vux.alert.show({content:'邀请码错误,请重新输入！'})
}

})
}, 1000)
}
},
components: {
XButton,
XHeader,
XInput,
Group
}
}



