
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data() {
return {
rootManage: '管理员',
nickname: '',
familyId: '',
admin: [],
role: [],
data: []
}
},
created() {
this.isManager()
},
methods: {
isManager(){
var that = this
this.$axios.post(`/SmartHome/is_manager`,{
familyId: that.$route.query.familyId,
userId: JSON.parse(localStorage.mydata).user.id
})
.then((res) => {
if(res.data == false){
this.$vux.alert.show({
content: '你不是管理员,不能操作！',
onShow(){
that.$router.go(-1)
}
})
}else{
this.clickInit()
}
})
},
clickInit(){
var that = this
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.get(`/SmartHome/get_family?id=${id}`)
.then((rs) => {
that.familyId = rs.data[0].id
this.$axios.get(`/SmartHome/get_member?id=${that.$route.query.familyId}`)
.then((res) => {
that.admin = res.data[0].创建者
that.role = res.data[0].普通成员
that.nickname = res.data[0].创建者[0].nickname
})
})
},
clickManage(){
var that = this
this.$router.push({path: '/my_family/roles_manage', query: {familyId: that.$route.query.familyId}})
}
},
components: {
XHeader,
Group,
Cell
}
}



