
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
data() {
return {
index: 0,
selected: '',
list2: [
{
imgUrl: 'http://xiaoxiangba.com/assets/img/scene.png',
name: '设备操作'
}, {
imgUrl: 'http://xiaoxiangba.com/assets/img/me.png',
name: '设备信息'
}
],
demo2: '设备操作',
data: [],
sensorList: [],
switchStatus: false,
options: {
color: ['#ff7f50', '#87cefa', '#da70d6', '#32cd32', '#6495ed',
'#ff69b4', '#ba55d3', '#cd5c5c', '#ffa500', '#40e0d0'],
backgroundColor: '#f8f8f8',
title: {
x: 'center',                 // 水平安放位置，默认为左对齐，可选为：


y: 'top',

backgroundColor: 'rgba(0,0,0,0)',
borderColor: '#ccc',       // 标题边框颜色
borderWidth: 0,            // 标题边框线宽，单位px，默认为0（无边框）
padding: 5,                // 标题内边距，单位px，默认各方向内边距为5，
itemGap: 10,               // 主副标题纵向间隔，单位px，默认为10，
textStyle: {
fontSize: 18,
fontWeight: 'bolder',
color: '#ff6666'          
},
text: '设备传感器信息'
},
tooltip: {
trigger: 'axis',
triggerOn: 'click',
axisPointer: {
type: 'line'
}
},
legend: {
x: 'center',
y: '30',
borderColor: '#6699FF',//边框颜色
textStyle: {
color: '#1e90ff'          
},
data: ['大修金额', '中修沥青砼金额', '预防性养护金额', '金额总计']
},
xAxis: {
name: '商品',
nameLocation: 'start',
nameGap: '50',
boundaryGap: true,
data: ['0:00', '6:00', '6:00', '6:00', '6:00', '6:00', '6:00']
},
yAxis: {
type: 'value',
scale: true,
name: '销量',
max: 100,
min: 0,
boundaryGap: [0.2, 0.2]
},
series: [{
name: '销量',
type: 'bar',
data: [5, 20, 36, 60, 10, 55],
smooth: true,
hoverAnimation: false,
symbol: 'rect',
itemStyle: {
normal: {
color: '#9f9fa0'
}
},
lineStyle: {
normal: {
color: '#9f9fa0',
type: 'dashed'
}
}
}]
},
option: {
title: {
x: '150',                 // 水平安放位置，默认为左对齐，可选为：


y: 'top',

backgroundColor: 'rgba(0,0,0,0)',
borderColor: '#ccc',       // 标题边框颜色
borderWidth: 0,            // 标题边框线宽，单位px，默认为0（无边框）
padding: 5,                // 标题内边距，单位px，默认各方向内边距为5，
itemGap: 10,               // 主副标题纵向间隔，单位px，默认为10，
textStyle: {
fontSize: 18,
fontWeight: 'bolder',
color: '#ff6666'          
},
text: '全省大中修资金统计'
},
color: ['#ff7f50', '#87cefa', '#da70d6', '#32cd32', '#6495ed',
'#ff69b4', '#ba55d3', '#cd5c5c', '#ffa500', '#40e0d0'],
tooltip: {trigger: 'axis'},

legend: {
x: 'center',
y: '30',
borderColor: '#6699FF',//边框颜色
textStyle: {
color: '#1e90ff'          
},
data: ['大修金额', '中修沥青砼金额', '预防性养护金额', '金额总计']
},

toolbox: {
x: '80%',
y: 'top',
show: true,
feature: {
mark: {show: true},

saveAsImage: {show: true},

dataView: {show: true},
}
},
calculable: true,
xAxis: {
data: []
},
yAxis: [
{
type: 'value',
name: '单位：万元',
min: '0',
max: '',
splitNumber: 20
}
],
series: [{
type: 'line',
name: '大修金额',
data: []
}, {
type: 'line',
name: '中修沥青砼金额',
data: []
}, {
type: 'line',
name: '预防性养护金额',
data: []
}, {
type: 'line',
name: '金额总计',
data: []
},
]
},
lightClass: true
}
},
created()
{
this.clickInit();
}
,
methods: {
clickInit()
{
var deviceId = this.$route.query.deviceId
this.$axios.get(`/SmartHome/get_device/${deviceId}`)
.then((res) => {
console.log(res);
this.sensorList = res.data[0].sensorList
this.data = res.data[0]
})
}
,
clickMore()
{
var deviceId = this.$route.query.deviceId
this.$router.push({path: 'del_device', query: {deviceId: deviceId}})
}
,
clickSensorList(id,name){
this.$router.push({path: '/device_sensors',query:{sensorsId: id,name: name}})
},
drawLine()
{
let that = this
let myChart = this.$echarts.init(document.getElementById('myEchart'))
myChart.setOption(that.option);
let data = []
for (let i = 0, min = 5, max = 99; i < 6; i++) {
data.push(Math.floor(Math.random() * (max + 1 - min) + min))
}
that.options.series[0].data = data
setTimeout(that.drawLine, 3000)
}
,
clickCode(id)
{
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$axios.post('/SmartHome/control_device', {
familyId: this.data.familyId,
deviceId: this.$route.query.deviceId,
operator: id
})
.then((res) => {
console.log(res)
if(res.data == 'Success'){
this.$vux.alert.show({
content: '发送成功!'
})
}else{
this.$vux.alert.show({
content: '发送失败!'
})
}
})
this.$vux.loading.hide()
}, 3000)
}
,
showLight()
{
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.lightClass = !this.lightClass
}, 3000)
}
}
,
mounted()
{

}
,
components: {
Group,
Cell,
Swiper,
SwiperItem,
Tabbar,
TabbarItem,
XHeader,
XTable,
XButton,
XSwitch,
LoadMore
}
}



