
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
XHeader,
XButton,
XInput,
Group
},
data() {
return {
phoneNum: '',
phoneCode: '',
code: '',
password: '',
showGet: true,
isGet: false,
timer: null,
count: '',
isOk: true,
isPwd: 'password',
isShow: true
}
},
created() {
this.clickGetCode()
},
methods: {
clickGetCode() {
this.phoneNum = this.$route.query.phone
this.$axios.post('/SmartHome/getCode', {phone: this.phoneNum})
.then((res) => {
console.log(res);
this.phoneCode = res.data
})
const TIME_COUNT = 60
if (!this.timer) {
this.count = TIME_COUNT
this.showGet = false
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--
} else {
this.showGet = true
clearInterval(this.timer)
this.timer = null
}
}, 1000)
}
},
clickOk() {
this.$vux.loading.show()
if (this.code == this.phoneCode) {
this.$axios.post('/SmartHome/register', {
phone: this.phoneNum,
password: this.password
})
.then((res) => {
if (res.data == 'registerSuccess') {
var that = this
setTimeout(() => {
this.$vux.loading.hide()
this.$vux.alert.show({
content: '注册成功!',
onHide () {
that.$router.push('/login')
},
})
}, 500)
}
})
} else {
setTimeout(() => {
this.$vux.loading.hide()
this.$vux.alert.show({
content: '验证码错误!'
})
}, 500)
}
},
clickShow() {
this.isShow = !this.isShow
if (this.isShow == false) {
this.isPwd = 'text'
} else {
this.isPwd = 'password'
}
}
},
watch: {
code: function () {
if (this.code.length != '' && this.password.length >= 6) {
this.isOk = false
} else {
this.isOk = true
}
},
password: function () {
if (this.code.length != '' && this.password.length >= 6) {
this.isOk = false
} else {
this.isOk = true
}
}
}
}



