
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data() {
return {
data: []
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var id = JSON.parse(localStorage.mydata).user.id
var familyId = parseInt(this.$route.query.familyId)
this.$axios.get(`/SmartHome/get_normal_member?userId=${id}&familyId=${familyId}`)
.then((res) => {
if(res.data !== 'getUserFail'){
this.data = res.data
}
})
},
clickDetele(deleteUserId){
var that = this;
var userId = JSON.parse(localStorage.mydata).user.id
var familyId = this.$route.query.familyId
this.$axios.post('/SmartHome/del_member', {
userId: userId,
familyId: familyId,
deleteUserId: deleteUserId
})
.then((res) => {
that.clickInit()
})
},
},
components: {
XHeader,
XButton,
Checklist,
Cell,
CellBox,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton
}
}



