
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

export default {
directives: {
TransferDom
},
created() {
},
data() {
return {
showToast: false,
show1: true,
show2: false,
show3: false,
percent2: 0,
strokeColor2: '#3FC7FA',
showDoc1: '',
showDoc2: '',
showDoc3: '',
}
},
methods: {
loadingTime() {
this.percent2 = 0
var self = this
if (this.show2 = true) {
for (var i = 0; i < 20; i++) {
setTimeout(function () {
self.percent2 += (i / 4)
if (self.percent2 === 30) {
this.showDoc1 = self.percent2
} else if (self.percent2 === 60) {
this.showDoc2 = self.percent2
} else if (self.percent2 === 100) {
self.show2 = false

self.$router.push('/user_device');
}
}, i * 1000)
}
}
}
},
components: {
Flow,
FlowState,
FlowLine,
XCircle,
XInput,
XDialog,
XImg,
XButton,
XHeader
}
}



