
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
data() {
return {
show4: false,
showFamily: false,
showDevice: false,
sceneId: 0,
sceneName: '',
data: [
{
id: 0,
name: '到家',
icon: 'https://xiaoxiangba.com/assets/img/gohome.png',
link: '/scene/gohome?id=0'
}, {
id: 1,
name: '离家',
icon: 'https://xiaoxiangba.com/assets/img/leavehome.png',
link: '/scene/gohome?id=1'
}, {
id: 2,
name: '起床',
icon: 'https://xiaoxiangba.com/assets/img/rise.png',
link: '/scene/gohome?id=2'
}, {
id: 3,
name: '休息',
icon: 'https://xiaoxiangba.com/assets/img/rest.png',
link: '/scene/gohome?id=3'
},],
familyData: [],
familyId: '',
familyName: '',
familyIndex: '',
deviceData: [],
deviceId: '',
deviceName: ''
}
},
components: {
XSwitch,
Confirm,
Popup,
XButton,
XHeader,
Group,
Cell
},
created() {
this.sceneId = this.$route.query.id
this.sceneName = this.data[this.sceneId].name
},
methods: {
clickSave(){
var that = this
this.$axios.post('/SmartHome/add_scene',
{
userId: JSON.parse(localStorage.mydata).user.id,
familyId: this.familyId,
deviceId: this.deviceId,
sceneName: this.sceneName,
pattern: this.deviceName
})
.then((res)=>{
console.log(res)
if(res.data == 'add scene failure'){
this.$vux.alert.show({
content: '保存失败！'
})
}else{
this.$vux.alert.show({
content: '保存成功！',
onHide(){
that.$router.push('/me')
}
})
}
})
},
clickCondition() {
this.$router.push('/scene/condition')
},
hideDevice(index, familyId) {
this.deviceId = familyId
this.deviceName = this.deviceData[index].name
this.showDevice = !this.showDevice
},
clickDevice() {
console.log(this.familyId);
this.$axios.get(`/SmartHome/get_family_device?familyId=${this.familyId}`)
.then((res) => {
console.log(res)
this.deviceData = res.data
})
this.showDevice = !this.showDevice
},
clickFamily() {
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.get(`/SmartHome/get_family?id=${id}`)
.then((res) => {
this.familyData = res.data
console.log(res)
})
this.showFamily = !this.showFamily
},
hideFamily(index, familyId) {
console.log(index, familyId);
this.showFamily = !this.showFamily
this.familyIndex = index
this.familyId = familyId
this.familyName = this.familyData[index].familyName
},
showPlugin() {
this.$vux.confirm.show({
title: '确定删除该场景吗',
content: '场景删除后，设备任务无法正常执行',
onShow() {
console.log('plugin show')
},
onHide() {
console.log('plugin hide')
},
onCancel() {
console.log('plugin cancel')
},
onConfirm() {
console.log('plugin confirm')
}
})
},
}
}



