
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
data() {
return {
bgImg: [
'/upload/family/bg01.jpg',
'/upload/family/bg02.jpg',
'/upload/family/bg03.jpg',
'/upload/family/bg04.jpg',
'/upload/family/bg05.jpg',
],
showDetail: false,
photo: [],
demo01: 0,
showComplete: false,
edit: '编辑',
lightClass: false,
data:[]
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var id = JSON.parse(localStorage.mydata).user.id
var familyId = parseInt(this.$route.query.familyId)
this.$axios.get(`/SmartHome/get_familyimg?userId=${id}&familyId=${familyId}`)
.then((res) => {
res.data === 'you have not privilege' ? '' : this.photo = res.data
})
this.$axios.get(`/SmartHome/get_sysimg?familyId=${familyId}`)
.then((res) => {
this.data = res.data
console.log(res)
})
},
clickChooseDefImg(id){
var familyId = this.$route.query.familyId;
this.$axios.post('/SmartHome/ud_familyimg', {
familyImgId: familyImgId,
familyId: familyId
})
.then((res) => {
this.$router.push('/home')
})
},
clickChooseImg(familyImgId){
var familyId = this.$route.query.familyId;
this.$axios.post('/SmartHome/ud_familyimg', {
familyImgId: familyImgId,
familyId: familyId
})
.then((res) => {
this.$router.push('/home')
})
},
clickDetail(id){
this.$axios.post('/SmartHome/del_familyimg', {
userId: JSON.parse(localStorage.mydata).user.id,
familyId: parseInt(this.$route.query.familyId),
familyImgId: id
})
.then((res) => {
var that = this
this.showDetail = false
setTimeout(function () {
that.clickInit()
}, 500)
this.showDetail = false
})
},
upload (e) {   // 上传照片
var familyId = parseInt(this.$route.query.familyId)
var userId = JSON.parse(localStorage.mydata).user.id
var files = e.target.files
for (var i in files) {
if (typeof files[i] !== 'number' && typeof files[i] !== 'function') {
var param = new FormData()
param.append('files', files[i], files[i].name)
param.append('userId', userId)
param.append('familyId', familyId)
var config = {
headers: {'Content-Type': 'multipart/form-data'}
};
this.$axios.post('/SmartHome/add_familyimg', param, config)
.then(res => {
console.log(res);
if(res.data == 'addFamilyImgFailure'){
this.$vux.alert.show({
content: '你不是管理员！不能上传'
})
}
this.clickInit();
})
}
}
},
consoleIndex() {

},
showEdit() {
this.showComplete = !this.showComplete
},
click(key) {
console.log(key)
}
},
components: {
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet
}
}



