
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
data() {
return {
options: {
color: ['#ff7f50', '#87cefa', '#da70d6', '#32cd32', '#6495ed',
'#ff69b4', '#ba55d3', '#cd5c5c', '#ffa500', '#40e0d0'],
backgroundColor: '#f8f8f8',
title: {
x: 'center',                 // 水平安放位置，默认为左对齐，可选为：


y: 'top',

backgroundColor: 'rgba(0,0,0,0)',
borderColor: '#ccc',       // 标题边框颜色
borderWidth: 0,            // 标题边框线宽，单位px，默认为0（无边框）
padding: 5,                // 标题内边距，单位px，默认各方向内边距为5，
itemGap: 10,               // 主副标题纵向间隔，单位px，默认为10，
textStyle: {
fontSize: 18,
fontWeight: 'bolder',
color: '#ff6666'          
},
text: '设备传感器信息'
},
tooltip: {
trigger: 'axis',
triggerOn: 'click',
axisPointer: {
type: 'line'
}
},
legend: {
x: 'center',
y: '30',
borderColor: '#6699FF',//边框颜色
textStyle: {
color: '#1e90ff'          
},
data: ['大修金额', '中修沥青砼金额', '预防性养护金额', '金额总计']
},
xAxis: {
name: '商品',
nameLocation: 'start',
nameGap: '50',
boundaryGap: true,
data: ['0:00', '6:00', '6:00', '6:00', '6:00', '6:00', '6:00']
},
yAxis: {
type: 'value',
scale: true,
name: '销量',
max: 100,
min: 0,
boundaryGap: [1, 1]
},
series: [{
name: '销量',
type: 'bar',
data: [5, 20, 36, 60, 10, 55],
smooth: true,
hoverAnimation: false,
symbol: 'rect',
itemStyle: {
normal: {
color: '#9f9fa0'
}
},
lineStyle: {
normal: {
color: '#9f9fa0',
type: 'dashed'
}
}
}]
},
option: {
title: {
x: '150',                 // 水平安放位置，默认为左对齐，可选为：


y: 'top',

backgroundColor: 'rgba(0,0,0,0)',
borderColor: '#ccc',       // 标题边框颜色
borderWidth: 0,            // 标题边框线宽，单位px，默认为0（无边框）
padding: 5,                // 标题内边距，单位px，默认各方向内边距为5，
itemGap: 10,               // 主副标题纵向间隔，单位px，默认为10，
textStyle: {
fontSize: 18,
fontWeight: 'bolder',
color: '#ff6666'          
},
text: '设备传感器信息'
},
color: ['#ff7f50', '#87cefa', '#da70d6', '#32cd32', '#6495ed',
'#ff69b4', '#ba55d3', '#cd5c5c', '#ffa500', '#40e0d0'],
tooltip: {trigger: 'axis'},
legend: {
x: 'center',
y: '30',
borderColor: '#6699FF',//边框颜色
textStyle: {
color: '#1e90ff'          
},
data: ['光照']
},
toolbox: {
x: '80%',
y: 'top',
show: true,
feature: {
mark: {show: true},

saveAsImage: {show: true},

dataView: {show: true},
}
},
calculable: true,
xAxis: {
name: '时间段/min',
nameLocation:'middle',//坐标轴名称显示位置。
nameTextStyle:{
padding:-10
},
axisLabel : {//坐标轴刻度标签的相关设置。
interval:0,
rotate:"45"
},
nameGap: '50',
boundaryGap: true,
data: []
},
yAxis: {
type: 'value',
scale: true,
name: '',
boundaryGap: [1, 1],
splitNumber:20,
min: 0
},
series: [{
type: 'line',
name: '光照',
data: []
}]
},
data: [],
topName: this.$route.query.name
}
},
created(){

},
methods: {
drawLine()
{
let that = this
let myChart = this.$echarts.init(document.getElementById('myEchart'))
myChart.setOption(that.option);





this.$axios.get(`/SmartHome/get_sensorsdata?sensorsId=${this.$route.query.sensorsId}`)
.then(res => {
let xAxisData = []
let seriesData = []
let max = 0
for (let i in res.data) {
if(res.data[i].dataValue > max){
max = res.data[i].dataValue
}
xAxisData.push(res.data[i].collectionTime.hours + ':' + res.data[i].collectionTime.minutes)
seriesData.push(Number(res.data[i].dataValue))
}
that.option.yAxis.name = res.data[0].dataName
console.log(res.data[0].dataName);
that.option.xAxis.data = xAxisData
that.option.series[0].data = seriesData
that.option.yAxis.max = Math.max.apply(Math, seriesData)
console.log(that.option.xAxis.data);
console.log(that.option.series[0].data);
})
setTimeout(that.drawLine, 3000)
},
clickMore(){
}
},
mounted()
{
this.drawLine()
},
components: {
XHeader
}
}



