
import TODO from '../common/TODO.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'


export default {
data () {
return {
todoStatus: false,
showStatus: '',
list: [],
data: [],
familyId: ''
}
},
created() {
this.clickInit()
},
methods: {
clickInit(){
this.familyId = this.$route.query.familyId
this.$axios.get(`/SmartHome/get_family_device?familyId=${this.familyId}`)
.then((res) => {
console.log(res)
if (res.data != 'searchDeviceFailure') {
this.data = res.data
}
})
},
clickControl(id) {
this.$router.push({path: '/device_detail', query: {deviceId: id}})
},
clickAddDevice(){
this.$router.push({path: '/all_device', query: {familyId: this.familyId}})
},
showTodo() {
this.todoStatus = false
}
},
components: {
XHeader,
Tabbar,
TabbarItem,
Cell,
CellBox,
CellFormPreview,
Panel,
Group,
Radio,
TODO
}
}



