
import TODO from '../common/TODO.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'


export default {
data () {
return {
todoStatus: false,
data: []
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var that =this
var id = JSON.parse(localStorage.mydata).user.id
var familyId = this.$route.query.familyId
this.$axios.get(`/SmartHome/get_device?userId=${id}&familyId=${familyId}`)
.then((res) => {
console.log(res)
if (res.data == 'can not get devices'){
this.$vux.alert.show({
content:'您不是管理员,不能添加设备！',
onShow(){
that.$router.go(-1)
}
})
}else{
this.data = res.data
}
})
},
clickAddDevice(deviceId){
var userId = JSON.parse(localStorage.mydata).user.id
var familyId = this.$route.query.familyId
this.$axios.post('/SmartHome/add_device', {
userId: userId,
familyId: familyId,
deviceId: deviceId
})
.then((res) => {
this.$router.push({path: '/user_device', query: {familyId: familyId}})
})
},
showTodo() {
this.todoStatus = false
}
},
components: {
XHeader,
Tabbar,
TabbarItem,
Cell,
CellBox,
CellFormPreview,
Panel,
Group,
Radio,
TODO
}
}



